@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Open+Sans:300,400);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  text-align: center;
  overflow: auto;
}
body {
  margin: 0;
}
h1, h2 {
  font-family: 'Abril Fatface', cursive;
  font-weight: normal;
}
ul {
  list-style-type: disc;
  padding: 0;
  width: 50%;
  margin: 20px auto;
  display: inline-block;
}
li {
  margin: 0 10px;
}
p {
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  font-size: 1.1em;
  line-height: 1.6em;
  text-align: left;
}
@media (max-width: 750px) {
p {
      font-size: 0.9em;
}
}
a {
  color: #e74c3c;
  text-decoration: none;
  font-weight: normal;
}
a:hover {
    text-decoration: underline;
}
.wrapper {
  width: 100%;
  margin: 0 auto;
  display: inline-block;
  overflow: hidden;
}
.header {
  background-color: #f0f0f0;
  display: inline-block;
  width: 100%;
  position: relative;
}
.header__bg {
  width: 100%;
  position: absolute;
  top: 0;
  display: block;
}
.header__bg svg {
    width: 100%;
    height: auto;
    fill: white;
}
@media (max-width: 375px) {
.header__bg {
      width: 240%;
}
}
.logo {
  color: #e74c3c;
  font-size: 5em;
  margin: 0.3em auto 0;
  position: relative;
}
@media (max-width: 750px) {
.logo {
      font-size: 4em;
      line-height: 1em;
}
}
.subtitle {
  font-size: 1.25em;
  position: relative;
  margin: 1.1em auto 1.5em;
}
@media (max-width: 750px) {
.subtitle {
      margin: 1.1em auto;
}
}

.home-view {
  background-color: #f0f0f0;
  padding: 0em 0 3em;
}
@media (max-width: 750px) {
.home-view {
      padding-bottom: 2em;
}
}
.intro {
  width: 70%;
  margin: 0em auto 2.5em;
  text-align: center;
  position: relative;
}
@media (min-width: 1440px) {
.intro {
      width: 60%;
}
}
@media (max-width: 1024px) {
.intro {
      width: 80%;
}
}
@media (max-width: 750px) {
.intro {
      text-align: left;
}
}
.home__contact--linkedin {
  display: inline-block;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.home__contact--linkedin svg {
    width: 2em;
    height: 2em;
}
.home__contact--linkedin .fil2 {
    fill: #2c3e50;
}
.home__contact--linkedin .fil0 {
    fill: #34495e;
}
.home__contact--linkedin .fil1 {
    fill: #ffffff;
}
.home__contact--linkedin:hover {
    opacity: 0.75;
}
.home__contact--linkedin:hover .fil2 {
      fill: #025E91;
}
.home__contact--linkedin:hover .fil0 {
      fill: #0273B3;
}

.portfolio-wrapper {
  /* margin-top: 5em; */
}

.project-component {
  text-align: left;
  padding-bottom: 10em;
  position: relative;
}
@media (max-width: 750px) {
.project-component {
      padding-bottom: 2.5em;
}
}
.project__image-wrapper {
  height: auto;
  width: 100%;
  background-color: black;
  display: block;
  text-align: center;
  position: relative;
  padding: 0.5em 0;
}
@media (max-width: 750px) {
.project__image-wrapper {
      padding: 0.25em 0;
}
}
.project__image-wrapper img {
    height: 100%;
    width: 80%;
    display: block;
    margin: 0 auto;
}
@media (max-width: 750px) {
.project__image-wrapper img {
        width: 100%;
}
}
.project__text-wrapper {
  display: block;
  position: relative;
  width: 100%;
}
.project__text-content {
  width: 80%;
  margin: 0 auto;
  display: block;
}
@media (min-width: 1440px) {
.project__text-content {
      width: 60%;
}
}
.project__text-content h2 {
    font-size: 4em;
    margin-bottom: 0.2em;
    display: inline-block;
}
@media (max-width: 750px) {
.project__text-content h2 {
        font-size: 2em;
        line-height: 1.1em;
}
}
.project__awards {
  list-style-type: none;
  width: 100%;
  margin: 0;
  text-align: center;
}
.project__awards li {
    display: inline-block;
    float: left;
    margin: 0 1.2em 0 0;
}
.project__bg {
  width: 100%;
  position: absolute;
  top: 0;
  display: block;
  z-index: -1;
}
.project__bg svg {
    width: 100%;
    height: auto;
    fill: #f0f0f0;
}
@media (max-width: 375px) {
.project__bg {
      width: 150%;
}
}

.award-component {
  width: 70px;
  margin-top: 1.2em;
}
.award__image {
  background-size: 100% auto;
}
.award__image img {
    width: 100%;
}
.award__prize {
  font-size: 0.5em;
  text-transform: uppercase;
  text-align: center;
}
